@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One&family=Montserrat:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,600&family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,200;1,300;1,400&family=Quicksand:wght@300;400;500;600;700&family=Raleway:ital,wght@0,200;1,200;1,400;1,500;1,600;1,700;1,900&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
  background-color: grey;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

p{
  background-color: blueviolet;
  color: aqua;
  font-size: 4rem;
  
}


.Type{
  padding: 4rem 0rem 0rem 0rem;
 
}
.header{
  display: flex;
  justify-content: space-around;
  align-items: center;
background-color:rgb(226, 43, 76) ;
font-size: 1.3rem;
}

.header .right{
display: flex;
}

.header .right .logo{
font-family: "Black Ops One", cursive;
font-size: 1.875rem;
}